<template>
    <div class="topic">
        <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad" :immediate-check="false">
            <van-row type="flex" justify="space-around">
                <van-col span="24" v-for="item in list" :key="item.id" class="topicCard" @click="godetailaction(item.id)">
                    <img :src="item.scene_pic_url" alt="" width="100%">
                    <h4>{{item.title}}</h4>
                    <p class="title">{{item.subtitle}}</p>
                    <p class="price">{{item.price_info}}元起</p>
                </van-col>
                <!-- <van-cell v-for="item in list" :key="item" :title="item" /> -->
            </van-row>
        </van-list>
        <div class="foot"></div>
    </div>
</template>

<script>
    import {
        listaction
    } from "@/api/topic/index"
    export default {
        data() {
            return {
                count: 1,
                list: [],
                loading: false,
                finished: false,

            };
        },
        computed: {

        },
        created() {

        },
        mounted() {
            this.init();
        },
        methods: {
            init() {
                listaction({
                    page: this.count,
                }).then(res => {
                    this.list.push(...res.data.data);
                    if (this.count > res.data.total) {
                        this.finished = true;
                    }
                });
                this.count++;
                this.loading = false;

            },
            onLoad() {
                this.init();

            },
            godetailaction(id) {
                this.$router.push(`/topic/detailaction?id=${id}`)

            },
        },
    }
</script>

<style scoped lang="scss">
    .topic {
        background-color: #f4f4f4;

        .topicCard {

            padding-bottom: 20px;

            h4 {
                margin: 10px 0px;
                width: 100%;
                font-size: 16px;
                text-align: center;
                font-weight: normal;
            }


            .title {
                width: 100%;
                text-align: center;
                margin: 5px 0px;
            }

            .price {
                text-align: center;
                color: red;

            }
        }

    }

    .foot {
        width: 100%;
        height: 50px;
        background-color: #fff;
    }

</style>